import { Component, OnInit } from '@angular/core';
import { GOOGLE_MAPS_DIRECTIVES, MouseEvent } from 'angular2-google-maps/core';

@Component({
  moduleId: module.id,
  selector: 'app-my-maps-project',
  templateUrl: 'my-maps-project.component.html',
  styleUrls: ['my-maps-project.component.css'],
  directives: [GOOGLE_MAPS_DIRECTIVES]
})
export class MyMapsProjectComponent implements OnInit {

  title: string = 'My first angular2-google-maps project';
  lat: number = 51.678418;
  lng: number = 7.809007;
  // google maps zoom level
  zoom: number = 8;

  markers: marker[] = [
  	  {
  		  lat: 51.673858,
  		  lng: 7.815982,
  		  label: 'A',
  		  draggable: true
  	  },
  	  {
  		  lat: 51.373858,
  		  lng: 7.215982,
  		  label: 'B',
  		  draggable: false
  	  },
  	  {
  		  lat: 51.723858,
  		  lng: 7.895982,
  		  label: 'C',
  		  draggable: true
  	  }
    ]

    clickedMarker(label: string, index: number) {
    console.log(`clicked the marker: ${label || index}`)
  }
  markerDragEnd(m: marker, $event: MouseEvent) {
    console.log('dragEnd', m, $event);
  }

  mapClicked($event: MouseEvent) {
    this.markers.push({
      lat: $event.coords.lat,
      lng: $event.coords.lng,
      draggable: true
    });
  }


  constructor() { }

  ngOnInit() {
  }

}
// just an interface for type safety.
interface marker {
  lat: number;
  lng: number;
  label?: string;
  draggable: boolean;
}
